import './App.css'
import styled from 'styled-components'
import { BrowserRouter as Router, NavLink, Route, Routes } from 'react-router-dom'
import { users, battles } from './objects'
import Battle from './pages/Battle'
import Pyramid from './pages/Pyramid'
import Participant from './pages/Participant'
import AllBattles from './pages/AllBattles'
import AllParticipant from './pages/AllParticipants'

const Wrapper = styled.div`
  margin: 0 auto;
  background: lightgrey;
  max-width: 1160px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
`;
  
const HeaderWrapper = styled.header`
  display: flex;
  align-items: center;
  min-height: 5vh;
  box-sizing: border-box;
  border-bottom: 1px black solid;
`;

const NavLinkStyled = styled(NavLink)`
  color: darkorange;
  margin-left: 10px;
`;

const Main = styled.div`
  min-height: 90vh;
`;

const Footer = styled.footer`
  box-sizing: border-box;
  border-top: 1px solid black;
  min-height: 5vh;
`;

function App() {
  return (
    <Router>
      <Wrapper>
        <HeaderWrapper>
          <NavLinkStyled className ='a' to='/ingame-test'>
            
          </NavLinkStyled>
          <NavLinkStyled to='/battles'>
             
          </NavLinkStyled>
          <NavLinkStyled to='/participants'>
             
          </NavLinkStyled>
        </HeaderWrapper>
        <Main>
        <Routes>
          <Route path='/' element={<Pyramid participants={users} battles={battles}/>} />
          <Route path='/ingame-test' element={<Pyramid participants={users} battles={battles}/>} />
          <Route path='/participants/:id' element={<Participant participants={users} battles={battles}/>}/>
          <Route path='/battles/:id' element={<Battle battles={battles}></Battle>}/>
          <Route exact path='/battles' element={<AllBattles battles={battles}></AllBattles>}/>
          <Route exact path='/participants' element={<AllParticipant participant={users}></AllParticipant>}/>
        </Routes>
        </Main>
        <Footer>
          footer
        </Footer>
      </Wrapper>
    </Router>
  );
}

export default App;
