import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { battles } from '../objects';

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
`;

const ScheduleWrapper = styled.div`
  width: 30%;
  background-color: lightblue;
`;

const PyramidWrapper = styled.div`
  text-align: center;
  overflow-y: auto;
  border: 2px red solid;
  min-height: 50px;
  padding: 0 1em;
  width: 70%;
`;

const PyramidRowWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: safe center;
  flex-wrap: wrap;
  border-bottom: 1px solid black;
`;

const ParticepantLink = styled(Link)`
  background-color: white;
  padding: .5em;
  margin: .5em;
  border: .1em solid transparent;
  text-decoration: none;
  color: black;

  &:hover {
    transition: .25s;
    cursor: pointer;
    border-color: black;
  }

  &:active {
    color: white;
    background-color: black;
  }

  &:visited {
    color: black;
  }
`;

const BattleCard = styled.div`
  box-sizing: border-box;
  background-color: white;
  border: 1px solid black;
  margin-bottom: 10px;
`;


const LinkH2 = styled(Link)`
  font-weight: bold;
  font-size 24px;
  color: black;
  text-decoration: none;

  &:visited {
    color: black;
  }

  &:hover {
    text-decoration: underline;
  }
`;

const LinkH3 = styled(Link)`
  margin: 2px;
  white-space: nowrap;
  font-style: italic;
  font-size 16px;
  color: black;
  text-decoration: none;

  &:visited {
    color: black;
  }

  &:hover {
    text-decoration: underline;
  }
`;

export default function Pyramid({participants}) {
  const participantsByRank = {};
 
  participants.filter(e => e.active === true)
    .forEach(p =>
      participantsByRank[p.userRank] ?
      participantsByRank[p.userRank].push(p) :
      participantsByRank[p.userRank] = [p]
    );

  const listRows = Object.keys(participantsByRank)
    .sort((a, b) => a-b)
    .map(rank => 
      <PyramidRowWrapper>
        {
          participantsByRank[rank].map(e =>
            <ParticepantLink to={`/participants/${e.userName}`}>{e.userName}</ParticepantLink>
          )
        }
      </PyramidRowWrapper>
    );
    
    const activeBattles = battles.filter(e => Date.now() < new Date(e.expDate));
    const activeBattlesCards = activeBattles.map(e => {
      return (
        <BattleCard>
          <LinkH2  to={`${`/battles/${e.battleName}`}`}>{e.battleName}</LinkH2>
          <h3>:</h3>
          {e.participants.map(p =>
            <LinkH3 to={`${`/participants/${p}`}`}>{p}</LinkH3>
          )}
          <h3> : {new Date(e.expDate).toString()}</h3>
        </BattleCard>
      );
    });

  return (
    <Wrapper>
      <ScheduleWrapper>
        {activeBattlesCards}
      </ScheduleWrapper>
      <PyramidWrapper>
        {listRows}
      </PyramidWrapper>
    </Wrapper>
  )
}